* {
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid #ccc;
  border-radius: 6px;
  margin: 10px;
  overflow: hidden;
}
.card p {
  font-size: 16px;
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}